.account-popup {
  --f7-popup-tablet-border-radius: 15px;
}
.account-page {
  --f7-navbar-height: 56px;
  --f7-list-button-text-align: left;
  --f7-list-margin-vertical: 20px;
}
.theme-dark .account-page {
  --f7-navbar-bg-color: #2C2C2E;
  --f7-navbar-bg-color-rgb: 50, 50, 50;
  --f7-page-bg-color: #1C1C1E;
  --f7-list-bg-color: #2C2C2E;
  --f7-block-strong-bg-color: #2C2C2E;
}
.account-page .account-user .item-inner {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}
.account-page .account-user .item-title {
  font-weight: normal;
}
.account-page .account-user .item-subtitle {
  opacity: 0.55;
}
.account-page .account-user img {
  width: 56px;
  height: 56px;
  border-radius: 50%;
}
